const prefixCls = "vv-list";
export default {
  name: "VList",
  provide() {
    return {
      ListInstance: this
    };
  },
  data() {
    this.keys = [];
    this.defaultPaginationProps = {
      current: 1,
      pageSize: 10,
      onChange: (page, pageSize) => {
        const {
          pagination
        } = this;
        this.paginationCurrent = page;
        if (pagination && pagination.onChange) {
          pagination.onChange(page, pageSize);
        }
      },
      total: 0
    };
    return {
      paginationCurrent: 1
    };
  },
  props: {
    header: String,
    footer: String,
    emptyText: {
      type: String,
      default: '暂无数据'
    },
    dataSource: {
      type: Array,
      default: []
    },
    bordered: {
      type: Boolean,
      default: false
    },
    itemLayout: {
      default: "horizontal" //horizontal或vertical
    },

    size: {
      type: String,
      default() {
        return "large"; //large 、small
      }
    },

    loading: {
      type: Boolean,
      default: false
    },
    split: {
      type: Boolean,
      default: true
    },
    pagination: {
      type: [Boolean, Object],
      default: false
    }
  },
  computed: {
    classString() {
      // large => lg
      // small => sm
      let sizeCls = "";
      switch (this.size) {
        case "large":
          sizeCls = "lg";
          break;
        case "small":
          sizeCls = "sm";
          break;
        default:
          break;
      }
      return [`${prefixCls}`, `${prefixCls}-${sizeCls}`, `${prefixCls}-${this.itemLayout}`, {
        [`${prefixCls}-bordered`]: this.bordered,
        [`${prefixCls}-split`]: this.split
      }];
    }
  },
  methods: {
    // 切换当前页
    currentChange(page) {
      this.pagination.currentChange(page);
    }
  }
};